/* Logs.css */

/* Component Container */
.logs-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    margin: 20px;
  }
  
  /* Component Title */
  .logs-title {
    color: #333;
    margin-bottom: 20px;
    width: 100%;
    color: white;
    background-color: #10132F;
    padding: 10px;
    border-radius: 5px;
  }
  
  /* Section Title */
  .section-title {
    font-size: 20px;
    margin-top: 20px;
    padding: 10px;
    color: white; /* Text color for all section titles */
    border-radius: 5px; /* Rounded corners for section titles */
    text-align: center; /* Center-align text within section titles */
  }
  
  /* Background colors for section titles */
  .accounts-title {
    background-color: #007bff; /* Blue color for Accounts */
  }
  
  .tag-fetch-title {
    background-color: #28a745; /* Green color for Tag Fetch */
  }
  
  .comments-title {
    background-color: #dc3545; /* Red color for Comments */
  }
  
  /* Data Table */
  .data-table {
    width: 100%;
    margin-top: 20px;
    overflow-x: auto;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  table th,
  table td {
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #ccc;
  }
  
  table th {
    background-color: #f5f5f5;
    color: #333;
    font-weight: bold;
  }
  
  table tbody tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  table tbody tr:hover {
    background-color: #e0e0e0;
  }
  