/* DataForm.css */

/* Container */
.container {
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f7f7f7;
    text-align: center; /* Center all content */
  }
  
  /* Header */
  .container h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  /* Section Headings */
  .section-heading {
    font-size: 20px;
    margin-top: 20px;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-weight: bold;
    text-align: center; /* Center section headings */
  }
  
  /* Account Entry */
  .account-entry {
    display: flex;
    justify-content: center; /* Center account fields horizontally */
    align-items: center;
    margin-bottom: 10px;
  }
  
  .account-entry input[type="text"] {
    width: 100%; /* 100% width for input tags */
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .account-entry button {
    background-color: #e74c3c;
    color: #fff;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-left: 10px;
  }
  
  .account-entry button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  /* Add Account Button */
  .add-account-button {
    background-color: #3498db;
    color: #fff;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    margin-left: 10px;
    transition: background-color 0.3s;
  }
  
  .add-account-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  /* Other Entry (Hashtags and Comments) */
  .other-entry {
    display: flex;
    justify-content: center; /* Center other fields horizontally */
    align-items: center;
    margin-bottom: 10px;
  }
  
  .other-entry input[type="text"] {
    width: 100%; /* 100% width for input tags */
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  /* Delete Button */
  .delete-button {
    background-color: #e74c3c;
    color: #fff;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-left: 10px;
  }
  
  .delete-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  /* Settings */
  .settings-entry {
    margin-top: 20px;
  }
  
  .settings-entry label {
    font-weight: bold;
    margin-bottom: 5px;
    text-align: center; /* Center label text horizontally */
    width: 100%;
    display: block;
  }
  
  .settings-entry input[type="number"] {
    width: 90%; /* 90% width for input tags */
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 5px;
  }
  
  /* Submit Button */
  .submit-button {
    background-color: #2ecc71;
    color: #fff;
    border: none;
    padding: 12px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
    font-size: 18px;
    transition: background-color 0.3s;
  }
  
  .submit-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  /* Success Popup */
  .success-popup {
    background-color: #2ecc71;
    color: #fff;
    border: 1px solid #27ae60;
    border-radius: 5px;
    padding: 10px;
    margin-top: 20px;
    text-align: center;
  }
  