/* Dashboard.css */

.dashboard-container {
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin: 20px;
}

.dashboard-title {
  background-color: #10132F;
  color: white;
  padding: 10px;
  border-radius: 5px;
}

.settings-title {
  background-color: #333333; /* Different color for Settings title */
  color: white;
  padding: 10px;
  border-radius: 5px;
}

.account-status-title {
  background-color: #2E4057; /* Different color for Account Status title */
  color: white;
  padding: 10px;
  border-radius: 5px;
}

.comment-status-title {
  background-color: #6a5acd; /* Different color for Comment Status title */
  color: white;
  padding: 10px;
  border-radius: 5px;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.data-table th {
  padding: 8px;
  text-align: left;
}

.data-table td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

.success {
  color: green;
}

.failed {
  color: red;
}



/* Add this CSS in your existing CSS file or create a new one */
.desktop-mode-message {
  background-color: #3498db; /* Change the background color to a different color */
  color: #fff; /* Text color */
  text-align: center;
  padding: 10px;
}

.desktop-mode-message p {
  margin: 0;
  font-size: 16px;
  font-weight: bold;
}

/* Add media query for mobile phones */
@media (max-width: 768px) {
  .desktop-mode-message {
    background-color: #e74c3c; /* Different background color for mobile devices */
  }
}

