/* App.js */
.App {
  text-align: center;
  padding: 20px;
}

.app-nav {
  background-color: #2c3e50; /* Change background color */
  border-radius: 5px;
  width: 100%;
  padding: 10px 0;
  display: flex; /* Align items horizontally */
  justify-content: center;
}

.app-nav__list {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.app-nav__item {
  margin: 0 20px;
}

.app-nav__link {
  text-decoration: none;
  color: #ecf0f1; /* Change text color */
  font-weight: bold;
  padding: 10px;
  transition: background-color 0.3s;
}

.app-nav__link:hover {
  background-color: #3498db; /* Change background color on hover */
  border-radius: 5px;
}

/* Active link */
.app-nav__link.active {
  background-color: #3498db; /* Change background color for active link */
  border-radius: 5px;
}
