

#ToggleButton {
  text-align: center;
  font-family: Arial, sans-serif;
  padding: 20px;
  background-color: #f5f5f5; /* Background color for the entire component */
}

#ToggleButton h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333; /* Text color for the heading */
}

#startButton,
#stopButton {
  padding: 10px 20px;
  font-size: 16px;
  margin: 10px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
  outline: none;
  width: 150px;
}

#startButton:disabled,
#stopButton:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  color: #777; /* Text color when disabled */
}

#startButton {
  background-color: #007bff; /* Blue color for 'Start Process' button */
  color: #fff; /* Text color for 'Start Process' button */
}

#stopButton {
  background-color: #dc3545; /* Red color for 'Stop Process' button */
  color: #fff; /* Text color for 'Stop Process' button */
}

#startButton:hover,
#stopButton:hover {
  transform: scale(1.05); /* Button scaling on hover */
}
